module.exports = ":host {\n  display: grid;\n  grid-gap: 0;\n  grid-template:\n    \"  list                  .      player               \" auto\n    /  minmax(200px, 1.5fr)  0.5fr  minmax(250px, 2.5fr)\n  ;\n  \n  height: 100vh;\n  overflow: hidden;\n  perspective: 70em;\n  background-color: lightblue;\n}\n\nvideo-list    { grid-area: list; }\n\nvideo-player  { grid-area: player; }\n\nvideo-list {\n  height: 130vh;\n  display: block;\n  background: black;\n  overflow-y: auto;\n  padding: 10px;\n  padding-top: 10vh;\n  padding-bottom: 10vh;\n  transform:\n    rotate(-5deg)\n    translate(85px, -15vh)\n    rotateY(30deg)\n  ;\n  box-shadow: 20px 15px 10px 0 rgba(0, 0, 0, 0.33);\n  outline: 15px solid black;\n  border-left: 15px solid;\n  border-right: 15px solid;\n  -o-border-image: repeating-linear-gradient(black, black 15px, lightblue 15px, lightblue 30px) 1;\n     border-image: repeating-linear-gradient(black, black 15px, lightblue 15px, lightblue 30px) 1;\n\n  /* Hide scrollbar on Firefox */\n  scrollbar-width: none;\n}\n\n/* Hide scrollbar on Chrome, Safari */\n\nvideo-list::-webkit-scrollbar {\n  display: none;\n}\n\n.highlighted {\n  background-color: rgb(255, 253, 210);\n}\n\n/*# sourceMappingURL=data:application/json;base64, */"